.form-container {
  background-color: #fdebe9;
  margin: auto;
  text-align: center;
  padding: 1vw 2vw;
}

.form-containers {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 0.7vw 2vw;
}

.p-form {
  background-color: transparent;
}

.right,
.left {
  display: flex;
  padding: 0.7vw;
  align-items: center;
  justify-content: center;
}

.right {
  flex-direction: column;
  width: 60%;
}

.contactForm-textarea {
  padding: 0.7vw;
  margin-bottom: 1vw;
  width: 60%;
  margin-top: 1vw;
  background-color: #fec8c4;
  border: 5px white solid;
}

@media only screen and (max-width: 480px) {
  .form-containers .left {
    flex-direction: column;
  }

  .form-containers .right {
    width: 90%;
  }

  .contactForm-textarea {
    width: 100%;
  }
}
