.awardImage {
  height: 4vw;
}

.pen {
  height: 30vw;
}

.feature {
  height: 2vw;
  margin: 2vw;
}

.contact-hand {
  height: 7vw;
  position: relative;
  right: 2vw;
  bottom: 4vw;
  z-index: 1;
  margin-top: 2vw;
  opacity: 0.6;
}

.about-woman {
  height: 20vw;
}

.product-image {
  height: 4vw;
}

.icon {
  height: 5vw;
}

.insta-icon {
  height: 4.2vw;
  margin: 1vw;
}

.p-choice-image {
  max-height: 10vw;
}

@media only screen and (max-width: 1000px) {
  .pen {
    height: 35vw;
  }
}

@media only screen and (max-width: 660px) {
  .feature {
    height: 3vw;
  }
  .pen {
    height: 40vw;
  }

  .about-woman {
    height: 30vw;
    margin: 5vw;
  }
}

@media only screen and (max-width: 480px) {
  .awardImage {
    height: 10vw;
  }

  .feature {
    height: 7vw;
    margin: 5vw 1vw;
  }

  .product-image {
    height: 8vw;
  }

  .about-woman {
    height: 50vw;
    margin: 5vw auto;
  }

  .icon {
    height: 10vw;
  }

  .p-choice-image {
    max-height: 30vw;
  }
}
